.modal{
    position: fixed;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-overlay{
    height: 100vh;
    width: 100vw;
    z-index: 2;
    background-color: rgba($color: #000000, $alpha: 0.5);
    position: fixed;
    top: 0;
    left: 0;
}

.modal-content{
    z-index: 3;

    width: min(400px, 90vw);
    padding: 1.6rem;
    border-radius: 16px;
    background-color: var(--modal-color);
    box-shadow: 0 0 16px 4px black;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    .button{
        position: absolute;
        top: 8px;
        right: 8px;
    }
    .modal-title{
        font-size: 3.2rem;
    }

    form{
        display: flex;
        flex-direction: column;
        gap: 1.6rem;
    }
}
